.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gg {
  z-index: 99;
  position: fixed;
  right: 10px;
  top: 15px;
  font-size: 20px;
  color: #000000;
  cursor: pointer;
  border: 1px solid #000000;
  background: #2E6FF2;
  padding: 7px 15px;
}

.gg i
{
  font-size: 20px;
  margin-bottom: 5px;
}



@media only screen and (max-width : 960px)
{
  .gg{
    right: 70px;
    top: 10px;
    position: absolute;
    position: fixed;
  }

  .gg i
  {
    font-size: 25px;
  }
}

@media only screen and (max-width : 400px)
{
  .gg
  {
    font-size: 10px;
    padding: 3px 10px;
    margin-top: -5px;
    margin-right: -40px;
    position: fixed;
  }

  .gg i
  {
    font-size: 10px;
  }
}