@import url('https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

.section {
  padding: 10rem 0 5rem 0;
  overflow: hidden;
}

.footer {
  background-color: #FFDEE9;
  background-image: linear-gradient(116deg, #FFDEE9 0%, #B5FFFC 100%);
  padding: 2rem 3rem;
  line-height: 2rem;
}

.footer-center span {
  margin-right: 1rem;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  color: #000;
  font-size: 17px;
  letter-spacing: 1px;
  font-weight: 300;
  transition: 0.6s;
}

.footer-center a:link,
.footer-center a:visited {
  display: block;
  color: #000;
  font-size: 17px;
  letter-spacing: 1px;
  font-weight: 300;
  transition: 0.6s;
}

.footer-center a:hover {
  color: #69ae14;
}

.footer-center div {
  color: #000;
  font-size: 1.4rem;
}

.footer-center h3 {
  font-size: 25px;
  font-weight: 700;
  margin-bottom: 20px;
}

.footer .payment-methods {
  margin-top: 10px;
}

.container11 {
  max-width: 114rem;
  margin: 0 auto;
}

.footer-center h5
{
    display: block;
    color: #000;
    font-size: 17px;
    letter-spacing: 1px;
    font-weight: 300;
    transition: 0.6s; 
}

@media (max-width: 1200px) {
  .container11 {
    padding: 0 3rem;
  }
}

@media (max-width: 768px) {
  .container11 {
    max-width: 60rem;
  }
}

@media (max-width: 998px) {
  .footer-container {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 2rem;
  }
}

@media (max-width: 768px) {
  .footer-container {
    grid-template-columns: 1fr;
    row-gap: 2rem;
  }
}