@import url('https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&family=Playfair+Display:wght@400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
*
{
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
    text-decoration: none;
}

.cart1
{
    position: absolute;
    top: 10vh;
    width: 100%;
}

.cart-container > h1{

	padding: 20px 0;
	text-transform: uppercase;

}

.cart{

	display: flex;

}

.cart-products{

	flex: 0.75;

}

.cart-product{

	display: flex;

	width: 90%;

	height: 200px;

	overflow: hidden;

	border: 1px solid silver;

	margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;

}

.cart-product:hover{

	border: none;

	box-shadow: 2px 2px 4px rgba(0,0,0,0.2);

	transform: scale(1.01);

}

.cart-product > img{

	width: 300px;

	height: 200px;

	object-fit: cover;

}

.cart-product > img:hover{

	transform: scale(1.04);

}

.product-info{

	padding: 20px;

	width: 100%;

	position: relative;

}

.product-name, .product-price, .product-offer{

	margin-bottom: 10px;

}
.cart-price
{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.product-price1
{
    color: #33d933;
    margin-left: 30px;
}

.product-remove{

	position: absolute;

	bottom: 20px;

	right: 20px;

	padding: 10px 25px;

	background-color: green;

	color: white;

	cursor: pointer;

	border-radius: 5px;

}

.product-remove:hover{

	background-color: white;

	color: green;

	font-weight: 600;

	border: 1px solid green;

}

.product-quantity {

	width: 40px;
	padding: 5px;
    margin-left: 70px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.qut-1
{
    outline: none;
    border: none;
    font-size: 20px;
    padding: 2px 10px;
    margin-left: 10px;
    background: #dd250c;
    border-radius: 50px;
}

.qut-2
{
    background-color: black;
    padding: 2px 10px;
    font-size: 20px;
    color: #fff;
    margin-left: 10px;
    border: none;
    border-radius: 5px;
}

.fa{

	margin-right: 5px;

}

.cart-total{


	flex: 0.25;

	margin-left: 20px;

	padding: 20px;

	height: 240px;

	border: 1px solid silver;

	border-radius: 5px;

}

.cart-total p{

	display: flex;

	justify-content: space-between;

	margin-bottom: 30px;

	font-size: 20px;

}

.cart-total a{

	display: block;

	text-align: center;

	height: 40px;

	line-height: 40px;

	background-color: tomato;

	color: white;

	text-decoration: none;

}

.cart-total a:hover{

	background-color: red;

}

@media screen and (max-width: 700px){

	.remove{

		display: none;

	}

	.cart-product{

		height: 150px;

	}

	.cart-product > img{

		height: 150px;

		width: 200px;

	}

	.product-name, .product-price, .product-offer{

		margin-bottom: 10px;

	}

}

@media screen and (max-width: 900px){

	.cart{

		flex-direction: column;

	}

	.cart-total{

		margin-left: 0;

		margin-bottom: 20px;

	}

}

@media screen and (max-width: 1220px){

	.cart-container{

		max-width: 95%;

	}

}