@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&family=Poppins:wght@200;300;400;500;600&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500;1,700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Dosis', sans-serif;
}

/* Home section start */
.home {
  position: absolute;
  top: 10vh;
  width: 100%;
}

.auto-slider {
  position: relative;
  box-sizing: content-box;
  display: inline-block;
  padding: 10px 10px 20px;
  background: #fff;
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

div#slider {
  overflow: hidden;
}

div#slider figure img {
  width: 20%;
  float: left;
}

div#slider figure {
  position: relative;
  width: 500%;
  margin: 0;
  left: 0;
  text-align: left;
  font-size: 0;
  animation: 35s sliding ease infinite;
}

.indicator {
  width: 100%;
  max-width: 100%;
  height: 6px;
  background: #e41b17;
  position: absolute;
  bottom: 0;
  animation: indicating 10s ease infinite;

}

@keyframes sliding {
  20% {
    left: 0%;
  }

  40% {
    left: -100%;
  }

  60% {
    left: -200%;
  }

  80% {
    left: -300%;
  }

  100% {
    left: 0%;
  }
}

@keyframes indicating {
  from {
    left: -100%;
  }

  to {
    left: 0;
  }
}

/* Home section end */

/* About section start */


.about-container {
  width: 100%;
  min-height: 80vh;
  background-color: #fcfafa;
  padding: 10px;
}

.about-container .title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-container .title h1 {
  font-size: 52px;
  color: #fff;
  background: -webkit-linear-gradient(43deg, #ff5757, #ffb53f, #fff100, #9ae900, #15ffe1, #816aff, #ff0040);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-stroke: 5px transparent;
  text-transform: uppercase;
}

.about-container .title h1::after {
  content: "";
  height: 6px;
  width: 150px;
  background-color: #85FFBD;
  background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
  border-radius: 6px;
  position: relative;
  display: block;
  margin: auto;
}

.content {
  width: 85%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
  margin: 30px auto;
}

.content .article {
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.2);
  background-color: #eee;
  padding: 25px 25px;
  transition: all 0.5s ease-out;
}

.content .article:hover {
  transform: scale(1.1);
  transition: all 0.5s ease-in;
}

.content .article h2 {
  color: #00c8ff;
  font-size: 35px;
  line-height: 1.5;
  letter-spacing: 1px;
}

.content .article p {
  margin-top: 30px;
  font-size: 19px;
  line-height: 1.5;
  color: #222;
}

.content .article a {
  font-size: 22px;
  text-decoration: none;
  display: inline-block;
  padding: 12px 35px;
  background-color: #cc0000;
  border-radius: 25px;
  color: #fff;
  border: 1px solid #cc0000;
  letter-spacing: 1.5px;
  margin-top: 35px;
}

.content .article a:hover {
  background-color: #fff;
  color: #cc0000;
  transition: 0.2s ease;
}

.img-section {
  width: 100%;
  margin: auto;
}

.img-section img {
  width: 90%;
  height: 500px;
  transition: all 0.5s ease-out;
}

.img-section img:hover {
  transform: scale(1.1);
  transition: all 0.5s ease-in;
}

@media screen and (max-width: 900px) {
  .content {
    grid-template-columns: repeat(1, 1fr);
  }

  .article {
    text-align: center;
  }

  .card-contant {
    width: 45%;
  }
}


/* About section end */

/* top sales section start */

.toprating {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.toprating h1 {
  font-size: 52px;
  background: -webkit-linear-gradient(43deg, #ff5757, #ffb53f, #fff100, #9ae900, #15ffe1, #816aff, #ff0040);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-stroke: 5px transparent;
  color: #fff;
  padding-top: 20px;
  text-transform: uppercase;
}

.toprating h1::after {
  content: "";
  height: 6px;
  width: 340px;
  background-color: #85FFBD;
  background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
  border-radius: 6px;
  position: relative;
  display: block;
  margin: auto;
}

.container10
{
    width: 88vw;
    margin: 0 auto;
}

.product-img 
img{
    width: 100%;
    display: block;
}
.lg-title,
.md-title,
.sm-title{
    font-family: 'Roboto', sans-serif;
    padding: 0.6rem 0;
    text-transform: capitalize;
}
.lg-title{
    font-size: 2.5rem;
    font-weight: 500;
    text-align: center;
    padding: 1.3rem 0;
    opacity: 0.9;
}
.md-title{
    font-size: 2rem;
    font-family: 'Roboto', sans-serif;
}
.sm-title{
    font-weight: 300;
    font-size: 1rem;
    text-transform: uppercase;
}
.text-light{
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.5;
    opacity: 0.5;
    margin: 0.4rem 0;
}

.products{
    background:#f8f9fa;
    padding: 3.2rem 0;
}
.products .text-light{
    text-align: center;
    width: 70%;
    margin: 0.9rem auto;
}
.product{
    margin: 1rem;
    position: relative;
    width: 80%;
}
.product-content{
    background: #ededed;
    padding: 3rem 0.5rem 2rem 0.5rem;
    cursor: pointer;
}
.product-img{
    background: rgba(255, 255, 255, 0.5);
    box-shadow: 0 0 20px 10px rgba(255, 255, 255, 0.5);
    width: 200px;
    height: 200px;
    margin: 0 auto;
    border-radius: 50%;
    transition: background 0.5s ease;
}
.product-btns{
    display: flex;
    justify-content: center;
    margin-top: 1.4rem;
    opacity: 0;
    transition: opacity 0.6s ease;
}
.btn-cart, .btn-buy{
    background: transparent;
    border: 1px solid black;
    padding: 0.8rem 0;
    width: 125px;
    font-family: inherit;
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    transition: all 0.6s ease;
}
.btn-cart{
    background: black;
    color: white;
}
.btn-cart:hover{
    background: #40c9a2;
}
.btn-buy{
    background: white;
}
.btn-buy:hover{
    background: #40c9a2;
    color: #fff;
}
.product-info{
    background: white;
    padding: 2rem;
}
.product-info-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.rating span{
    color: #eaa001;
}
.product-name{
    color: black;
    display: block;
    text-decoration: none;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: bold;
}
.product-price{
    padding-top: 0.6rem;
    padding-right: 0.6rem;
    display: inline-block;
}
.product-price:first-of-type{
    text-decoration: line-through;
    color: #9b1000;
}
.product-img img{
    transition: transform 0.6s ease;
}
.product:hover .product-img img{
    transform: scale(1.1);
}
.product:hover .product-img{
    background: #40c949;
}
.product:hover .product-btns{
    opacity: 1;
}
.off-info .sm-title{
    background: #40c9a2;
    color: white;
    display: inline-block;
    padding: 0.5rem;
    position: absolute;
    top: 0;
    left: 0;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    z-index: 1;
    letter-spacing: 3px;
    cursor: pointer;
}

/* product collection */
.product-collection{
    padding: 3.2rem 0;
}
.product-collection-wrapper{
    padding: 3.2rem 0;
}
.product-col-left{
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url(/src/image/fashion-img-1.jpg) center/cover no-repeat;
}
.product-col-r-top{
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url(/src/image/fashion-img-2.png) center/cover no-repeat;
}
.flex{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 50vh;
    padding: 2rem 1.5rem 3.2rem;
    margin: 5px;
}
.product-col-r-bottom > div:first-child{
    background: #00d5ff;
}
.product-col-r-bottom > div:last-child{
    background: #00ff4c;
}
.product-col-content{
    text-align: center;
    color: white;
}
.product-collection .text-light{
    opacity: 1;
    font-size: 0.8;
    font-weight: 400;
    line-height: 1.7;
}
.btn-dark{
    background: black;
    color: white;
    outline: 0;
    border-radius: 25px;
    padding: 0.7rem 1rem;
    border: 0;
    margin-top: 1rem;
    cursor: pointer;
    transition: all 0.6s ease;
    font-size: 1rem;
    font-family: inherit;
}
.btn-dark:hover{
    background: #40c9a2;
}







/* Media Queries */
@media screen and (min-width: 992px){
    .product-items{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .product-col-r-bottom{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
@media screen and (min-width: 1200px){
    .product-items{
        grid-template-columns: repeat(3, 1fr);
    }
    .product{
        margin-right: 1rem;
        margin-left: 1rem;
    }
    .products .text-light{
        width: 50%;
    }
}

@media screen and (min-width: 1336px){
    .product-items{
        grid-template-columns: repeat(4, 1fr);
    }
    .product-collection-wrapper{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .flex{
        height: 60vh;
    }
    .product-col-left{
        height: 121.5vh;
    }
}

/* top sales section end */

