@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;600;700;800&family=Orelega+One&family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Tapestry&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    transition: all 0.5s ease-in-out;
    text-decoration: none;
}

.about {
    position: absolute;
    top: 10vh;
    width: 100%;
}

.bg-image {
    width: 100%;
    height: 300px;
    background-image: url(/src/image/about-bg.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: blur(2px);
    -webkit-filter: blur(2px);
    overflow: hidden;
}

.bg-text {
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
    color: white;
    font-weight: bold;
    border: 2px solid #f1f1f1;
    position: absolute;
    top: 9%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    width: 50%;
    padding: 40px;
    text-align: center;
}

.bg-text h2 {
    font-size: 52px;
    color: #000;
    background: -webkit-linear-gradient(43deg, #ff5757, #ffb53f, #fff100, #9ae900, #15ffe1, #816aff, #ff0040);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-stroke: 5px transparent;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 5px;
    letter-spacing: 1px;
}

.bg-text p {
    font-size: 20px;
    color: #000;
    background: -webkit-linear-gradient(43deg, #ff5757, #ffb53f, #fff100, #9ae900, #15ffe1, #816aff, #ff0040);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-stroke: 5px transparent;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 10px;
    letter-spacing: 1px;
}

.about-para p {
    padding-top: 50px;
    color: #666;
    line-height: 30px;
    width: 60%;
    height: 20vh;
    font-size: 20px;
    margin-left: 20%;
    margin-right: 20%;
    text-align: center;
    padding-bottom: 50px;
}

.about-container1
{
    width: 1140px;
    max-width: 100%;
    margin: 0 auto;
}

.about-container1 .col-div-6
{
    width: 50%;
    float: left;
}

.clearfix{
	clear: both;
}

.a-img
{
    width: 100%;
    border-radius: 10px;
}

.about-heading1
{
    font-size: 29px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #060606;
}

.about-text
{
    font-size: 13px;
    letter-spacing: 1px;
    color: #060606;
    margin-bottom: 20px;
}

.about-name
{
    text-transform: uppercase;
    font-weight: 600;
    color: #a1a1a1;
    font-size: 14px;
}

.a-img-1
{
    width: 100%;
    height: 160px;
    border-radius: 10px;
    margin-top: 7px;
}

.about-heading-1
{
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #060606;
}