* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
}

.blog-container {
    width: 100%;
    position: absolute;
    top: 10vh;
}

.banner-section {
    background-image: url(/src/image/blog-bg.jpg);
    height: 300px;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
}

.blog-heading {
    color: #000;
    font-size: 40px;
    letter-spacing: 1px;
    margin-top: 14%;
    text-align: center;
    background: -webkit-linear-gradient(43deg, #ff5757, #ffb53f, #fff100, #9ae900, #15ffe1, #816aff, #ff0040);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-stroke: 5px transparent;
    text-transform: uppercase;
}

.container-blog {
    width: 1140px;
    max-width: 100%;
    margin: 0 auto;
}

.container-blog .col-div-6 {
    width: 50%;
    float: left;
}

.clearfix{
	clear: both;
}

.b-img {
    width: 100%;
    border-radius: 10px;
}

.heading1 {
    margin-top: 20px;
    color: #7b4ee4;
    font-size: 14px;
    text-transform: uppercase;
}

.blog-heading1 {
    font-size: 29px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #060606;
}

.blog-text {
    font-size: 13px;
    letter-spacing: 1px;
    color: #060606;
    margin-bottom: 20px;
}

.blog-name {
    text-transform: uppercase;
    font-weight: 600;
    color: #a1a1a1;
    font-size: 14px;
}

.lr-box {
    padding: 10px 20px;
}

.b-img-1 {
    width: 100%;
    height: 160px;
    border-radius: 10px;
    margin-top: 7px;
}

.blog-heading-1 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #060606;
}

.line {
    width: 100%;
    margin-top: 15px;
    border: none;
    height: 1px;
    background: #ddd;
}

.lr-box:hover
{
    opacity: 0.5;
	cursor: pointer;
}

.box-1:hover
{
    opacity: 0.5;
	cursor: pointer;
}

.blog-stories
{
    text-align: center;
}

.box-2
{
    width: 28.5%;
	float: left;
	margin: 25px;  
}

.box-2:hover{
	opacity: 0.5;
	cursor: pointer;
}
