@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.navbar {
    background-color: #FFDEE9;
    background-image: linear-gradient(116deg, #FFDEE9 0%, #B5FFFC 100%);
    width: 100%;
    height: 10vh;
    font-size: 25px;
    position: fixed;
    z-index: 99;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.26);
}

.nav-logo {
    font-size: 30px;
    justify-self: start;
    text-decoration: none;
    color: #fe671f;
    font-weight: 800;
    cursor: pointer;
    margin-left: 20px;
    position: absolute;
    top: 15px;
}

.nav-logo span {
    color: #662400;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 20px;
    width: 100%;
    list-style: none;
    justify-content: center;
    position: absolute;
    top: 20px;
}


.link {
    color: #000000;
    text-decoration: none;
    font-size: 20px;
    font-weight: 300;
    padding: 10px 20px;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    transition: all 0.5s ease-out;
    font-family: 'Poppins', sans-serif;
}

.link::before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: #fe671f;
    border-radius: 10px;
    height: 4px;
    transition: all 0.5s ease-in;
    -webkit-transition-property: left, right;
    transition-property: left, right;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.link:hover::before,
.link:focus::before,
.link:active::before {
    left: 0;
    right: 0;
}

.link:hover {
    color: #fe671f;
}



label {
    color: #000000;
    font-size: 20px;
    font-weight: 200;
    padding: 9px 17px;
    border-radius: 5px;
    transition: all 0.3s ease;
}

label {
    display: none;
}

label:hover {
    background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
}

input[type="checkbox"] {
    display: none;
}

ul ul {
    position: absolute;
    background-color: #FFDEE9;
    background-image: linear-gradient(116deg, #FFDEE9 0%, #B5FFFC 100%);
    top: 70px;
    width: 150px;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    list-style: none;
}

li:hover>ul {
    top: 45px;
    opacity: 1;
    visibility: visible;
    transition: all 0.3s ease;
    z-index: 1;
    list-style: none;
    border-radius: 5px;
}

ul ul li a {
    display: block;
    width: 100%;
    text-decoration: none;
    text-align: center;
    border-bottom: 1px solid #000000;
    color: #000000;
    line-height: 40px;
    border-radius: 0px !important;
}

ul ul li a:hover {
    background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
}

ul ul ul {
    position: absolute;
    top: 0;
    right: calc(-140% + 8px);
    border-radius: 5px;
    width: 200px;
}

ul ul li {
    position: relative;
}

ul ul li:hover ul {
    top: 0;

}

.menu-icon {
    display: none;
}

.menu-bar,
.menu-time {
    font-size: 30px;
}


@media only screen and (max-width : 960px) {
    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        font-size: 30px;
        transform: translate(-50%, 30%);
        cursor: pointer;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 10vh;
        left: 100%;
        transition: all .4s ease;
        overflow: auto;
        opacity: 0;
    }

    .nav-menu.active {
        background-color: #FFDEE9;
        background-image: linear-gradient(116deg, #FFDEE9 0%, #B5FFFC 100%);
        left: 0;
        transition: all .5s ease;
        z-index: 1;
        opacity: 1;
    }

    .link {
        text-align: center;
        padding: 10px;
        width: 100%;
        display: table;
        margin-top: 10px;
    }

    .link:hover {
        background-color: #85FFBD;
        background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
        border-radius: 0;
    }

    .nav-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 30%);
        margin-left: 0;
    }

    .search-box {
        margin-right: 140px;
    }

    .add-to-cart {
        margin-right: 70px;
    }

    label {
        line-height: 40px;
        font-size: 20px;
        display: block;
        padding: 8px 18px;
        cursor: pointer;
        margin-top: 30px;
        text-align: center;
        margin-left: 30px;
        font-family: 'Poppins', sans-serif;
        font-weight: 300;
        display: none;
    }

    .desktop-link {
        /* display: none; */
        line-height: 40px;
        font-size: 20px;
        display: block;
        padding: 8px 18px;
        cursor: pointer;
        margin-top: 30px;
        text-align: center;
        font-family: 'Poppins', sans-serif;
        font-weight: 300;
    }

    ul ul ul {
        position: static;
        opacity: 1;
        visibility: visible;
        background: none;
        max-height: 0px;
        overflow: hidden;
        z-index: 1;
        width: 150px;
    }


    ul #show-features:checked~ul,
    ul #show-items:checked~ul {
        max-height: 100vh;
    }

    ul #show-features:checked~ul,
    ul #show-items1:checked~ul {
        max-height: 100vh;
    }

    ul #show-features:checked~ul,
    ul #show-items2:checked~ul {
        max-height: 100vh;
    }

    ul ul li {
        list-style: none;
    }

    ul ul li a {
        font-size: 18px;
        line-height: 30px;
        border-radius: 5px !important;
    }


}

@media only screen and (max-width : 400px) {
    .nav-logo {
        font-size: 1.5rem;
        margin-top: -15px;
        margin-left: -20px;
    }

    .menu-icon, .menu-time,.menu-bar {
        font-size: 22px;
        transform: translate(1%, 25%);
        margin-top: -10px;
        margin-right: 5px;
    }
}