@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&family=Libre+Bodoni:ital,wght@0,400;1,400;1,500&family=Poppins:wght@200;300;400;500;600&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500;1,700&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    transition: all .2s linear;
    text-decoration: none;
    font-family: Arial, Helvetica, sans-serif;
    transition: all 0.4s cubic-bezier(0, 1.57, .67, 1.18);
}

.shop {
    padding-top: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
}

.shop-title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
}

.shop-title h1 {
    font-size: 52px;
    color: #fff;
    background: -webkit-linear-gradient(43deg, #ff5757, #ffb53f, #fff100, #9ae900, #15ffe1, #816aff, #ff0040);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-stroke: 5px transparent;
    text-transform: uppercase;
}

.shop-title h1::after {
    content: "";
    height: 6px;
    width: 200px;
    background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
    border-radius: 6px;
    position: relative;
    display: block;
    margin: auto;
}

.shop-container
{
    min-height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 40px 0;
}

.shop-container .shop-card
{
    height: 450px;
    width: 300px;
    overflow: hidden;
    position: relative;
    margin: 20px;
    border: 1px solid black;
    border-radius: 30px;
}

.shop-card img
{
    width: 100%;
    height: 70%;
    object-fit: cover;
}

.shop-card .shop-info
{
    text-align: center;
    line-height: 25px;
}

.shop-card .shop-info h3
{
    color: #333;
    padding-top: 5px;
}

.shop-star
{
    text-align: center;
}

.shop-star i
{
    color: gold;
    padding: 10px 0;
}

.shop-card .shop-price2
{
    display: flex;
    align-items: center;
    justify-content: center;
}

.shop-price2 .shop-price1
{
    font-size: 20px;
    color: #f00333;
    letter-spacing: 1px;
    text-align: center;
    text-decoration: line-through;
}

.shop-card .shop-price
{
    font-size: 20px;
    color: #2af663;
    letter-spacing: 1px;
    text-align: center;
    margin-left: 30px;
}

.shop-card .shop-discount
{
    position: absolute;
    top: 15px;
    left: 15px;
    height: 50px;
    width: 50px;
    display: block;
    background: rgba(255, 0, 0, 0.6);
    color: #fff;
    font-weight: bold;
    border-radius: 50%;
    text-align: center;
    line-height: 50px;
}

.shop-card .shop-panel 
{
    position: absolute;
    top: 15px;
    right: -50%;
    width: 45px;
    background: rgba(255, 255, 255, .8);
    display: flex;
    align-items: center;
    flex-flow: column;
    opacity: 0;
}

.shop-card:hover .shop-panel
{
    right: 15px;
    opacity: 1;
}

.shop-card .shop-panel a
{
    font-size: 20px;
    color: #333;
    margin: 15px 0;
}

.shop-card .shop-panel a:hover
{
    color: rgba(255, 0, 0, 0.9);
}

.shop-card button 
{
    position: absolute;
    left: 50%;
    bottom: 0%;
    transform: translateX(-50%);
    height: 40px;
    width: 140px;
    border: none;
    outline: none;
    background: rgba(0, 0, 0, .6);
    color: #fff;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    opacity: 0;
    transition-delay: .2s;
}

.shop-card:hover button
{
    opacity: 1;
    bottom: 30%;
}

.shop-card button:hover
{
    background: rgba(255, 0, 0, 0.6);
}
