
*
{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
}

.contact
{
    position: absolute;
    top: 10vh;
    width: 100% ;
}

.contact-bg
{
    background-image: url(/src/image/contact-img.jpg);
    height: 300px;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.contact-head
{
    width: 80%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
}

.contact-head h2
{
    font-size: 25px;
    font-weight: 700;
    padding: 5px;
}

.contact-head p
{
    padding: 5px;
    color: #666;
}

.contact-banner
{
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    height: 500px;
    background: #fff;
    margin-bottom: 50px;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.33);
}

.contact-img
{
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-img img
{
    width: 400px;
    height: 300px;
}

.contact-text h3
{
    text-align: center;
    font-size: 23px;
    letter-spacing: 1px;
    font-weight: 400;
}

.log-button
{
    padding-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.log-button button
{
    padding: 10px 40px;
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    background: #fb8339;
    cursor: pointer;
}